import { useState, useEffect } from 'react';
import * as MovieAPI from '../fetch.js';

export default function MovieSubCast({ movieId }) {
  const [movieCast, setMovieCast] = useState([]);
  useEffect(() => {
    MovieAPI.fetchMovieByIdCast(movieId).then(cast => setMovieCast(cast.cast));
  }, [movieId]);

  return (
    movieCast && (
      <>
        <ul>
          {movieCast.map(cast => (
            <li key={cast.id}>
              {cast.profile_path && (
                <img
                  src={`https://image.tmdb.org/t/p/w500/${cast.profile_path}`}
                  alt={`${cast.name}`}
                  width="100"
                />
              )}

              <p>{cast.name}</p>
              <p>{cast.character}</p>
            </li>
          ))}
        </ul>
        {movieCast.length === 0 && (
          <p>We don't have any casts for this movie</p>
        )}
      </>
    )
  );
}
